
import $ from 'jquery';

window.addEventListener('DOMContentLoaded', function() {

let list = document.querySelector('.list');
let closeAfter = document.querySelector('.close_cart_after');
let cart_icon = document.querySelector('.cart_icon');
let closeCartList = document.querySelector('.close_cart_list');
    let favItems = document.querySelector('.wish-items');
    let wishListButton = document.querySelector('.openWishListButton');
    let wLCountNumber = document.querySelector('.wish-number');
    let inputEmail = document.querySelector('.sign-email');
  let emailText = document.querySelector('.signEmailTxt');
let inputPassword = document.querySelector('.sign-password');
  let passTxt = document.querySelector('.signPassTxt');
  let signInContainer = document.querySelector('.signIn-container');
 let  allSpan =  document.getElementsByClassName('menu-button');
 let viewInfo = document.querySelector('.viewProduct');
  let addToWishListButton = document.querySelector('.add-to-wishlist');
  let increase = 0; // used to know when the menu is expanded
  let input_1 = document.querySelector('.input_1');
  let filters = document.querySelector('.filters');
  let signInUp = document.querySelector('.signInUp');
  let backDropFade = document.querySelector('.backdrop-fade');
  let  body =  document.querySelector('body');
  let chat_icon = document.querySelector('.chat-icon');

  

   // Wishlist items number
    let wrapWLItems = document.querySelector('.wrap-wl-items');
   let allWLItems = wrapWLItems.querySelectorAll('.info-wl-icon');



// eventlistener for 'view product'. Hide wishlist if displayed
viewInfo.addEventListener('click', function() {
favItems.style.display = 'none';
wLCountNumber.style.display = 'none';
wishListButton.style.display = 'none';
 
});
// open and close the right box with added cart list

cart_icon.addEventListener('mouseover', function() {
  
	
  if(signInContainer.style.display === 'block') {
    list.style.display = 'none';
  } else {
    list.style.display = 'block';
  }

});

cart_icon.addEventListener('mouseleave', function() {
	if(list.style.display === 'block' && signInContainer.style.display === 'none') {
    list.style.display = 'block';

  } else if(list.style.display === 'none' && signInContainer.style.display === 'none') {
    list.style.display = 'block';
  }
});

closeCartList.addEventListener('click', function() {
    list.style.display = 'none';
    wishListButton.style.display = 'block';
    favItems.style.display = 'block';


    // when wishlist is closed, display the wishlist counter IF Wishlist items number > 0 .
    if(allWLItems.length > 0) {
  wLCountNumber.style.display = 'block';
}
    
});
closeAfter.addEventListener('click', function() {
   list.style.display = 'none';
    wishListButton.style.display = 'block';
    favItems.style.display = 'block';
    

// when wishlist is closed, display the wishlist counter IF Wishlist items number > 0 .
    if(allWLItems.length > 0) {
    window.setTimeout(function() {
  wLCountNumber.style.display = 'block';
},1000);
}
  
});



// trigger and hide the WishList when clicking somewhere else
document.querySelector( '#container:not([class="wish-items"])' ).addEventListener('click' ,function() {
  if(favItems.style.right === '-0.4%') {
    favItems.style.right = '-100%';
  }
  wishListButton.style.display = 'block';

// remove the modal open class to be able to scroll
  body.classList.remove('modal-open');


if(window.innerWidth < 630 && chat_icon.style.display === 'none') {
      chat_icon.style.display = 'block';
}

});

// event listener - if clicking on container and the hamburger menu is displayed, hide the hamb menu
document.querySelector( '#container:not([class="navbar"])' ).addEventListener('click' ,function() {
let toggle = document.getElementById('navbarTogglerDemo03');

if(toggle.classList.contains('show')) {
  toggle.classList.remove('show');
  increase = 0; // reset the increase, to set all the nav bar styles to default

  // remove styles that has been added previously for menu hamburger styiling
  cart_icon.removeAttribute('style');
  navSignIn.removeAttribute('style');
  twos.removeAttribute('style');
  signIn.removeAttribute('style');

  if(navSignIn.style.display === 'none') {
    navSignIn.style.display = 'block';
  }
} else {
  return false;
}

});
/* -------------- SIGN IN CONTAINER -------------- */



document.querySelector('.wrap-contact-cont').addEventListener('click', function() {
if(list.style.display === 'block') {
  list.style.display = 'none';
}

wishListButton.style.display = 'block';
favItems.style.display = 'block';

if(addToWishListButton.style.color === 'red') {
     wLCountNumber.style.display = 'block';
}
// on click, hide the wishlist if was displayed
 if( favItems.style.right === '-0.4%') {
   favItems.style.right = '-100%';
  }

  if(window.innerWidth < 630) {
    let toggle = document.getElementById('navbarTogglerDemo03');
  toggle.classList.remove('show');
  increase = 0;

  cart_icon.removeAttribute('style');
  navSignIn.removeAttribute('style');
  twos.removeAttribute('style');
  signIn.removeAttribute('style');

  }
});

document.querySelector('.info-help-contact ').addEventListener('click', function() {
if(list.style.display === 'block') {
  list.style.display = 'none';
}
wishListButton.style.display = 'block';
favItems.style.display = 'block';

// if an item was added to wishlist/ if wishlist length > 0 , display the wishcounter
if(addToWishListButton.style.color === 'red') {
      wLCountNumber.style.display = 'block';
}
// on click, hide the wishlist if was displayed
 if( favItems.style.right === '-0.4%') {
   favItems.style.right = '-100%';
  }
});
document.querySelector('.nav-bar').addEventListener('click', function() {
if(list.style.display === 'block') {
  list.style.display = 'none';
}
wishListButton.style.display = 'block';
favItems.style.display = 'block';

if(addToWishListButton.style.color === 'red') {
       wLCountNumber.style.display = 'block';
}
// on click, hide the wishlist if was displayed
 if( favItems.style.right === '-0.4%') {
   favItems.style.right = '-100%';
  }
});
document.querySelector('.items').addEventListener('click', function() {
if(list.style.display === 'block') {
  list.style.display = 'none';
}
wishListButton.style.display = 'block';
favItems.style.display = 'block';

if(addToWishListButton.style.color === 'red') {
       wLCountNumber.style.display = 'block';
}
// on click, hide the wishlist if was displayed
 if( favItems.style.right === '-0.4%') {
   favItems.style.right = '-100%';
  }
});
document.querySelector('.filters').addEventListener('click', function() {
if(list.style.display === 'block') {
  list.style.display = 'none';
}
wishListButton.style.display = 'block';
favItems.style.display = 'block';

if(addToWishListButton.style.color === 'red') {
      wLCountNumber.style.display = 'block';
}
// on click, hide the wishlist if was displayed
 if( favItems.style.right === '-0.4%') {
   favItems.style.right = '-100%';
  }
});
  

/* -------------- SIGN IN CONTAINER -------------- */



// animate to default the input email box when focus is out
document.querySelector( '#signIn-container:not([id="sign-email"])' ).addEventListener('click' ,function() {
 if(inputEmail.value.length < 1) {
  emailText.style.top = '17.5%';
   emailText.style.transform = 'scale(1)';

 } else {
  return  false;
   }
 });

document.querySelector( '#signIn-container:not([id="sign-password"])' ).addEventListener('click' ,function() {
 if(inputPassword.value.length < 1) {
  passTxt.style.top = '52.5%';
  passTxt.style.transform = 'scale(1)';

 } else {
  return  false;
  }
});




// ------- HIGHTLIGHT THE CURRENT BUTTON MENU OF CURRENT PAGE - ----- //

 for(let i = 0; i < allSpan.length; i++) {
  allSpan[i].addEventListener('click', function() {

var current = document.getElementsByClassName("active");
  current[0].className = current[0].className.replace(" active", "");

  this.className += " active";


  });

 }




 // hide the INPUT FORM when clicking the hamb button and window size > 823
let toggleMenu = document.getElementById('toggleMenu'),
  navSignIn = document.querySelector('.nav-signIn'),
  twos = document.querySelector('.twos'),
  signIn = document.querySelector('.signInUp');


// fix the responsive style while clicking on the hamburger menu button
  // create dynamic content/design/responsive when window size is x
toggleMenu.addEventListener('click', function () {
  increase++; // increase by one the menu

  if (window.innerWidth > 823 && increase === 1) {
    input_1.style.display = 'none';
    navSignIn.style.display = 'none';
  } else if (window.innerWidth > 823 && increase === 2) {
    input_1.style.display = 'block';
    navSignIn.style.display = 'block';
    increase = 0;
  } else if (428 < window.innerWidth && window.innerWidth <= 600 && increase === 1) {
    twos.style.marginRight = '25%';
    twos.style.marginLeft = '-3%';

    navSignIn.style.width = '65%';
    navSignIn.style.right = '-6%';
    navSignIn.style.marginLeft = '-5%';


  } else if (428 < window.innerWidth && window.innerWidth <= 600 && increase === 2) {
    navSignIn.removeAttribute('style');
    twos.removeAttribute('style');
    increase = 0;
  } else if (600 < window.innerWidth && window.innerWidth <= 699 && increase === 1) {

    navSignIn.style.marginLeft = '-2%';
    twos.style.marginRight = '15%';
    cart_icon.style.marginLeft = '-8%';

  } else if (600 < window.innerWidth && window.innerWidth <= 699 && increase === 2) {
    cart_icon.removeAttribute('style');
    navSignIn.removeAttribute('style');
    twos.removeAttribute('style');
    increase = 0;
  } else if (699 < window.innerWidth && window.innerWidth <= 782 && increase === 1) {
    navSignIn.style.marginLeft = '-11%';
  } else if (699 < window.innerWidth && window.innerWidth <= 782 && increase === 2) {
    navSignIn.removeAttribute('style');
    increase = 0;
  } else if (782 < window.innerWidth && window.innerWidth <= 876 && increase === 1) {

    navSignIn.style.marginLeft = '-20%';
    twos.style.marginRight = '28%';

  } else if (782 < window.innerWidth && window.innerWidth <= 876 && increase === 2) {

    navSignIn.removeAttribute('style');
    twos.removeAttribute('style');
    increase = 0;


  } else if (window.innerWidth < 410 && increase === 1) {
    twos.style.width = '75%';
    twos.style.marginLeft = '1%';
    twos.style.left = '-15%';
    twos.style.marginBottom = '5px';
    cart_icon.style.left = '-12%';
    signIn.style.display = 'block';
    navSignIn.style.width = '100%';
    navSignIn.style.right = '-10%';


  } else if (window.innerWidth < 410 && increase === 2) {
    cart_icon.removeAttribute('style');
    navSignIn.removeAttribute('style');
    twos.removeAttribute('style');
    signIn.removeAttribute('style');
    increase = 0;


  } else if (409 < window.innerWidth < 428 && increase === 1) {
    twos.style.marginRight = '21%';
    navSignIn.style.marginLeft = '-5%';
  } else if (409 < window.innerWidth < 428 && increase === 2) {
    navSignIn.removeAttribute('style');
    twos.removeAttribute('style');
    increase = 0;
  }


});


// on window resize, close the cart list
window.addEventListener('resize', function () {


if(window.innerWidth > 650) {
// hide the filters if displayed on med devices
  filters.classList.remove('showFilter');
  filters.classList.add('hideFilter');

 // hide the shop list (right corner) if displayed
  if (list.style.display === 'block') {
    list.style.display = 'none';
    favItems.style.right = '-100%';
  } 
  // show the wishlist button
  wishListButton.style.display = 'block';
  // hide the favorite items if displayed
  favItems.style.right = '-100%';

  // hide sign in container ahd the black transparent background (backDropFade);

 // signInContainer.style.display = 'none';
  backDropFade.style.display = 'none'; 

 
  }
   
  // on width 992, fix the hiding issue and hide the input_1
  if(window.innerWidth >= 992) {
    input_1.style.display = 'none';

  }



 if(window.innerWidth > 630) {
  // on window resize, hide the expanded menu and set the increase to 0.
  let toggle = document.getElementById('navbarTogglerDemo03');
  toggle.classList.remove('show');
  increase = 0;

  cart_icon.removeAttribute('style');
  navSignIn.removeAttribute('style');
  twos.removeAttribute('style');
  signIn.removeAttribute('style');
}


if(window.innerWidth > 630) {
   let filters =  document.querySelector('.filters');
   let mobileFilter = document.querySelector('.mobileFilter');
   let openMobileFilter = document.querySelector('.openMobileFilter');

        // on resize; if window size <= 897, hide de collapsed menu
          // change the innerhtml button and styles
   filters.classList.remove('show');
   mobileFilter.innerHTML = 'Open filter';
   openMobileFilter.style.backgroundColor = 'white';
   openMobileFilter.style.color = 'black';

  }




});


// close hamburger menu when clicking the SignInUp button

signInUp.addEventListener('click', function() {
  let toggle = document.getElementById('navbarTogglerDemo03');



 toggle.classList.remove('show');
increase = 0; // reset the increase, to set all the nav bar styles to default


// remove styles that has been added previously for menu hamburger styiling
  cart_icon.removeAttribute('style');
  navSignIn.removeAttribute('style');
  twos.removeAttribute('style');
  signIn.removeAttribute('style');

});


// close the hamburger menu when clicking the menu
let menuButton = document.querySelector('.nav-item');

menuButton.addEventListener('click', function() {
  let toggle = document.getElementById('navbarTogglerDemo03');



 toggle.classList.remove('show');
increase = 0; // reset the increase, to set all the nav bar styles to default


// remove styles that has been added previously for menu hamburger styiling
  cart_icon.removeAttribute('style');
  navSignIn.removeAttribute('style');
  twos.removeAttribute('style');
  signIn.removeAttribute('style');

    // scroll to the top 
      $("html, body").scrollTop($('#top').offset().top);

});







// when scrolly > 1000 , hide the filter container on the left if displayed
window.addEventListener('scroll', function(){
 
    if(window.scrollY > 1000 ) {
      if(document.querySelector('.filters').classList.contains('showFilter')){
        document.querySelector('.filters').classList.remove('showFilter');
        document.querySelector('.filters').classList.add('hideFilter');
        document.querySelector('.items').style.left = '10.5%';
      }
      }

})

// on input focus, hide the chat icon (design issue on mobilesize)
let input_form = document.querySelector('.input_form');

input_form.addEventListener('focus', function(){
  chat_icon.style.display = 'none';
})


let chatOpen = document.querySelector('.need-help-chat');
let subChat = document.querySelector('.submit-chat-msg');
let wrapChatTxt = document.querySelector('.wrap-chat-text');



  window.addEventListener('resize', function() {
  if(chatOpen.style.display === 'block') {
      
      if(window.innerHeight < 400) {
        wrapChatTxt.style.maxHeight = '69%';
        subChat.style.padding = '7px';
         chatOpen.style.transitionProperty = 'none';
          subChat.style.transitionProperty = 'none';
          wrapChatTxt.style.transitionProperty = 'none';
      } else {
        wrapChatTxt.style.maxHeight = '77%';
         subChat.style.padding = '12px';

      }
 
 }
  })




});





