decrease(ev,index) {


  let totalCartNumber = [...this.state.totalCartNumber];
  let priceCart = [...this.state.priceCart];
   let list = document.getElementById('list');
 let lilist = list.getElementsByClassName('cart_li');
 let ldsRoller = document.querySelector('.lds-roller');


// if the item is equal to 2 (inside array, but not in the UI) set to the selected item totalPrice = price
  if(ev.piece === 2) {
     ev.totalPrice = ev.price;
}
  if(this.state.cart.length > 0 && ev.piece !== 1) {
      // decrease piece number item
    ev.piece--;
      // find the index of selected item price
    let find = priceCart.indexOf(ev);
      // delete from the array the index of selected price item (find)
    priceCart.splice(find,1);
    console.log(priceCart);  
      // splice one from the counting array to get the total cart items
    ev.morePieces.length -= 1;
      // splice -1 from the totalCartNumber to be counted
    totalCartNumber.length -= 1;
      // get the total sum to be displayed to selected total price
    let spliceSum = ev.piece * ev.price; // multiplicate the ev.piece with the ev.price and get the totalPrice
    ev.totalPrice = spliceSum.toFixed(2); // set the item.totalPrice
  
  this.setState({
    priceCart: priceCart,
    totalCartNumber: totalCartNumber
                })


} else {
  // returning fals to not activate the 'already in cart' message.
  return false;

 }

// add animated/pause effect after the item is multiplicated

  for(let i=0; i<lilist.length;i++) {
    lilist[i].style.opacity = '0.5';
    lilist[i].style.pointerEvents = 'none';
    ldsRoller.style.display = 'block';

    window.setTimeout(function() {
        lilist[i].style.opacity = '1';
        ldsRoller.style.display = 'none';
        lilist[i].style.pointerEvents = 'visible';
    },1000);
  }
}

