checkAgree(e) {
  let proceedButton = document.querySelector('.proceed-button'),
 firstName_val = document.querySelector('.firstName_val').value.length,
lastName_val = document.querySelector('.lastName_val').value.length,
street_value = document.querySelector('.street_value').value.length,
city_value = document.querySelector('.city_value').value.length,
country_value = document.querySelector('.country_value').value.length,
zipCode_value = document.querySelector('.zipCode_value').value.length,
phone_value = document.querySelector('.phone_value').value.length,
sdeliver = document.querySelector('.sdeliver').checked,
expdeliver = document.querySelector('.expdeliver').checked,
checkdPayOption = document.querySelectorAll('#checkradio :checked'),
inputEmail = document.querySelector('.input-email'),
enterValidMail = document.querySelector('.enter_valid_mail');


    // check if all the inputs are not empty and if everything is checked
  if(e.target.checked === false && this.state.cart.length === 0) {
    proceedButton.style.opacity = '0.4';
  } else if(e.target.checked === true &&  // agree box
    this.state.cart.length !== 0 && // cart not empty
    firstName_val > 3 && // text inputs
    lastName_val > 3 && 
    street_value > 3 &&    
    city_value > 3 &&   // |
    country_value > 3 && 
    zipCode_value > 3 && 
    phone_value > 3 && // text inputs
    checkdPayOption.length > 0 && // payment box
    inputEmail.value.length > 12 && // email input
    sdeliver === true || // delivery check box
    expdeliver === true) { // delivery check box
    proceedButton.style.opacity = '1'; // Proceed button -> visible ( if visible => check Proceedbutton() function | )
  } else if(e.target.checked === true && this.state.cart.length === 0) {
    proceedButton.style.opacity = '0.4';
  } else if(this.state.cart.length !== 0 && e.target.checked === false) {
    proceedButton.style.opacity = '0.4';
  }

 // if email is not valid, display the not valid message
  if(inputEmail.value.length < 13) {
    enterValidMail.style.display = 'block';
    window.setTimeout(function() {
        enterValidMail.style.display = 'none';
    },4000);
  }


}
