submitButton() {
 let firstName = document.querySelector('.first_name_input'),
 lastName = document.querySelector('.last_name_input'),
 email = document.querySelector('.email_input'),
 textArea = document.querySelector('.textArea_input'),
 loading = document.querySelector('.lds-dual-ring'),
 submitButton = document.querySelector('.submit-button'),
 markedItems = document.querySelector('.marked-items'),
 inputForm = document.querySelectorAll('#input-form-div .loop_input'),
 phoneInput = document.querySelector('.phone_input'),
 orderInput = document.querySelector('.order_input'),
 afterSubmitMsg = document.querySelector('.after-submit-message');

// loop through the submit inputs
for(let i = 0; i < inputForm.length; i++) {
    // if one input is empty => .red border
if(inputForm[i].value.length === 0) {
    inputForm[i].style.border = '1px solid red';
   // delay border color
    window.setTimeout(function() {
        inputForm[i].style.border = '1.5px solid red';
        inputForm[i].style.transition = '0.5s all ease';
    },400);
      window.setTimeout(function() {
        inputForm[i].style.border = '1px solid red';
        inputForm[i].style.transition = '1s all ease';
    },600);
 // if all importants inputs value length are higher than 3, submit the request.
} else if(firstName.value.length >= 3 && 
          lastName.value.length >= 3 && 
          email.value.length >= 3 && 
          textArea.value.length >= 3) {

          // display the loading style
          loading.style.display = 'block';
           window.setTimeout(function() {  
                     loading.style.display = 'none'; // hide the loading effect after 3 sec
                      submitButton.style.display = 'none'; // - || - 
                        markedItems.style.display = 'none'; // - || -
                },2000);

          inputForm[i].style.border = '1px solid black'; // if borders were red and now the inputs !== empty; border = black;
         inputForm[i].setAttribute('disabled', true); // after submits are done, disable all the inputs
         orderInput.setAttribute('disabled', true);
         phoneInput.setAttribute('disabled', true);

         window.setTimeout(function() {
            afterSubmitMsg.style.opacity = '1';
      },2500);
       // if the previous empty input was red and empty; border = black;
} else if(inputForm[i].value.length >= 3 && inputForm[i].style.border === '1px solid red') {
    inputForm[i].style.border = '1px solid black';
}
}
} 

